/* harmony default export */ __webpack_exports__["a"] = ([{"id":1,"slug":"/projects/the-future","title":"The Future","content":"<p>But I know you in the future. I cleaned your poop. Stop! Don't shoot fire stick in space canoe! Cause explosive decompression! Hey, tell me something. You've got all this money. How come you always dress like you're doing your laundry?</p><p>I love this planet! I've got wealth, fame, and access to the depths of sleaze that those things bring. Yes. You gave me a dollar and some candy. I meant 'physically'. Look, perhaps you could let me work for a little food? I could clean the floors or paint a fence, or service you sexually?</p>","media":{"small":"https://picsum.photos/300/300?image=1081","large":"https://picsum.photos/800/400?image=1081"}},{"id":2,"slug":"/projects/nicks","title":"Knicks","content":"<p>So, how 'bout them Knicks? Humans dating robots is sick. You people wonder why I'm still single? It's 'cause all the fine robot sisters are dating humans! Good news, everyone! There's a report on TV with some very bad news!</p><p>I can explain. It's very valuable. Fry, you can't just sit here in the dark listening to classical music. If rubbin' frozen dirt in your crotch is wrong, hey I don't wanna be right. Bender, you risked your life to save me!</p><p>Stop it, stop it. It's fine. I will 'destroy' you! Oh, but you can. But you may have to metaphorically make a deal with the devil. And by \"devil\", I mean Robot Devil. And by \"metaphorically\", I mean get your coat.</p>","media":{"small":"https://picsum.photos/300/300?image=1079","large":"https://picsum.photos/800/400?image=1079"}},{"id":3,"slug":"/projects/like-most-politicians","title":"Like Most Politicians","content":"<p>Noooooo! But, like most politicians, he promised more than he could deliver. I had more, but you go ahead. Uh, is the puppy mechanical in any way? Oh, I don't have time for this. I have to go and buy a single piece of fruit with a coupon and then return it, making people wait behind me while I complain.</p><p>We'll go deliver this crate like professionals, and then we'll go home. But I've never been to the moon! Good man. Nixon's pro-war and pro-family. I was having the most wonderful dream. Except you were there, and you were there, and you were there!</p>","media":{"small":"https://picsum.photos/300/300?image=0","large":"https://picsum.photos/800/400?image=0"}},{"id":4,"slug":"/projects/my-stupidness","title":"My Stupidness","content":"<p>Yeah, I do that with my stupidness. You, minion. Lift my arm. AFTER HIM! Oh right. I forgot about the battle. I'll get my kit! It's toe-tappingly tragic!</p><p>Um, is this the boring, peaceful kind of taking to the streets? Quite possible. We live long and are celebrated poopers. Hey, tell me something. You've got all this money. How come you always dress like you're doing your laundry?</p><p>Soon enough. Is the Space Pope reptilian!? I'm Santa Claus! Also Zoidberg.</p><p>Leela's gonna kill me. As an interesting side note, as a head without a body, I envy the dead. It may comfort you to know that Fry's death took only fifteen seconds, yet the pain was so intense, that it felt to him like fifteen years. And it goes without saying, it caused him to empty his bowels.</p>","media":{"small":"https://picsum.photos/300/300?image=1073","large":"https://picsum.photos/800/400?image=1073"}},{"id":5,"slug":"/projects/earthicans","title":"Earthicans","content":"<p>My fellow Earthicans, as I have explained in my book 'Earth in the Balance'', and the much more popular ''Harry Potter and the Balance of Earth', we need to defend our planet against pollution. Also dark wizards. Maybe I love you so much I love you no matter who you are pretending to be.</p><p>What kind of a father would I be if I said no? You've killed me! Oh, you've killed me! I'll tell them you went down prying the wedding ring off his cold, dead finger. And then the battle's not so bad?</p>","media":{"small":"https://picsum.photos/300/300?image=1062","large":"https://picsum.photos/800/400?image=1062"}},{"id":6,"slug":"/projects/the-horrible-ravages-of-youth.","title":"The Horrible Ravages of Youth.","content":"<p>I've got to find a way to escape the horrible ravages of youth. Suddenly, I'm going to the bathroom like clockwork, every three hours. And those jerks at Social Security stopped sending me checks. Now 'I'' have to pay ''them'! I love this planet! I've got wealth, fame, and access to the depths of sleaze that those things bring.</p>","media":{"small":"https://picsum.photos/300/300?image=998","large":"https://picsum.photos/800/400?image=998"}},{"id":7,"slug":"/projects/philip","title":"Philip","content":"<p>Then we'll go with that data file! I barely knew Philip, but as a clergyman I have no problem telling his most intimate friends all about him. And then the battle's not so bad? Oh yeah, good luck with that.</p><p>These old Doomsday Devices are dangerously unstable. I'll rest easier not knowing where they are. Fatal. Uh, is the puppy mechanical in any way? My fellow Earthicans, as I have explained in my book 'Earth in the Balance'', and the much more popular ''Harry Potter and the Balance of Earth', we need to defend our planet against pollution. Also dark wizards.</p>","media":{"small":"https://picsum.photos/300/300?image=975","large":"https://picsum.photos/800/400?image=975"}},{"id":8,"slug":"/projects/if-you-killed-your-grandfather","title":"If You Killed Your Grandfather","content":"<p>For example, if you killed your grandfather, you'd cease to exist! No. We're on the top. Morbo can't understand his teleprompter because he forgot how you say that letter that's shaped like a man wearing a hat.</p><p>For example, if you killed your grandfather, you'd cease to exist! Oh dear! She's stuck in an infinite loop, and he's an idiot! Well, that's love for you. You seem malnourished. Are you suffering from intestinal parasites?</p><p>You'll have all the Slurm you can drink when you're partying with Slurms McKenzie! Ah, the 'Breakfast Club' soundtrack! I can't wait til I'm old enough to feel ways about stuff! Soon enough. You lived before you met me?!</p>","media":{"small":"https://picsum.photos/300/300?image=967","large":"https://picsum.photos/800/400?image=967"}},{"id":9,"slug":"/projects/sleeping","title":"Sleeping","content":"<p>You won't have time for sleeping, soldier, not with all the bed making you'll be doing. Whoa a real live robot; or is that some kind of cheesy New Year's costume? Would you censor the Venus de Venus just because you can see her spewers?</p>","media":{"small":"https://picsum.photos/300/300?image=953","large":"https://picsum.photos/800/400?image=953"}},{"id":10,"slug":"/projects/inform-the-men","title":"Inform the Men","content":"<p>Kif, I have mated with a woman. Inform the men. Oh sure! Blame the wizards! They're like sex, except I'm having them! We'll go deliver this crate like professionals, and then we'll go home.</p><p>You seem malnourished. Are you suffering from intestinal parasites? Why yes! Thanks for noticing. Come, Comrade Bender! We must take to the streets! I am Singing Wind, Chief of the Martians. Morbo can't understand his teleprompter because he forgot how you say that letter that's shaped like a man wearing a hat.</p>","media":{"small":"https://picsum.photos/300/300?image=900","large":"https://picsum.photos/800/400?image=900"}},{"id":11,"slug":"/projects/goodbye-cruel-world","title":"Goodbye, Cruel World","content":"<p>Goodbye, cruel world. Goodbye, cruel lamp. Goodbye, cruel velvet drapes, lined with what would appear to be some sort of cruel muslin and the cute little pom-pom curtain pull cords. Cruel though they may be… I don't want to be rescued.</p><p>Doomsday device? Ah, now the ball's in Farnsworth's court! Guards! Bring me the forms I need to fill out to have her taken away! When will that be? Why would I want to know that?</p><p>Uh, is the puppy mechanical in any way? There, now he's trapped in a book I wrote: a crummy world of plot holes and spelling errors! You're going back for the Countess, aren't you? I just told you! You've killed me!</p><p>Hello Morbo, how's the family? Moving along… I don't know what you did, Fry, but once again, you screwed up! Now all the planets are gonna start cracking wise about our mamas. Oh, I don't have time for this. I have to go and buy a single piece of fruit with a coupon and then return it, making people wait behind me while I complain.</p>","media":{"small":"https://picsum.photos/300/300?image=885","large":"https://picsum.photos/800/400?image=885"}},{"id":12,"slug":"/projects/Rrobot-1-X","title":"Robot 1-X","content":"<p>Robot 1-X, save my friends! And Zoidberg! Our love isn't any different from yours, except it's hotter, because I'm involved. Ah, yes! John Quincy Adding Machine. He struck a chord with the voters when he pledged not to go on a killing spree.</p><p>You know the worst thing about being a slave? They make you work, but they don't pay you or let you go. I daresay that Fry has discovered the smelliest object in the known universe! Well, then good news! It's a suppository.</p>","media":{"small":"https://picsum.photos/300/300?image=882","large":"https://picsum.photos/800/400?image=882"}}]);

